import styled from "styled-components";
import { axiosInstance } from "../api/axios";
import { useState } from "react";
import { useAlertStore } from "../zustand/useAlertStore";

interface SubmitType {
    readonly userId: string;
    readonly password: string;
    readonly content: string;
}

interface GuestbookEntry {
    id: number;
    userId: string;
    password: string;
    content: string;
    date: string; 
  }

interface CommentFieldProps {
    setGuestBookData: (data: GuestbookEntry[]) => void;
    guestBookData: GuestbookEntry[];
  }

const CommentField = ({ guestBookData,setGuestBookData }: CommentFieldProps) => {

    const [submitData, setSubmitData] = useState<SubmitType>({userId: '', password: '', content: ''});
    const showSuccessAlert = useAlertStore((s) => s.showSuccess)
    const showWarnAlert = useAlertStore((s) => s.showWarn);

    const handleSubmit = async() => {
        if(!submitData.content || !submitData.password || !submitData.userId) {
            return showWarnAlert('  .')
        }
        try{
            const response = await axiosInstance.post('/guestbook', {
                "userId": submitData.userId,
                "password": submitData.password,
                "content": submitData.content
            })
            setSubmitData({userId: '', password: '', content: ''})
            setGuestBookData([...guestBookData, response.data])
            showSuccessAlert('  .')
        }catch(err){
            console.log(err)
        }
    }

    return(
    <CommentFieldContainer>
        <RowWrapper style = {{marginBottom: '1rem'}}>
            <VisitorInput placeholder="" value={submitData.userId} onChange = {(e) => {setSubmitData({...submitData, userId: e.target.value})}} type='text'/>
            <VisitorInput placeholder="" value={submitData.password}  onChange = {(e) => {setSubmitData({...submitData, password: e.target.value})}} type="password"/>
        </RowWrapper>
        
        <RowWrapper>
            <CommentArea placeholder="  !   " value={submitData.content}  onChange = {(e) => {setSubmitData({...submitData, content: e.target.value})}}/>
            <SubmitButton onClick = {handleSubmit}>
                
            </SubmitButton>
        </RowWrapper>
    </CommentFieldContainer>
    )
}

export default CommentField;

const CommentFieldContainer = styled.div`
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 3%;
    width: 85%;
    background-color: #F2F2F2;
    padding: 1rem;
`

const CommentArea = styled.textarea`
    width: 90%;
    border-radius: 8px;
    border: none;
    padding: 0.75rem;
`

const SubmitButton = styled.button`
    padding: 15px;
    width: 4rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem;
    background-color: white;
    border-radius: 5px;
    border: none;
`

const RowWrapper = styled.div`
    display: flex;
    align-items: flex-end;
`

const VisitorInput = styled.input`
    width: 8rem;
    height: 1.2rem;
    margin-right: 1rem;
    border-radius: 4px;
    border: none;
`


