import styled from "styled-components";
import FramerMotion from '../../source/webp/framer-motion.webp'
import React from '../../source/webp/React.webp'
import Zustand from '../../source/webp/Zustand.webp'
import styledComponent from '../../source/webp/styledcomponents.webp'
import CustomTooltip from "../../component/CustomTooltip";
import TpyeScript from '../../source/webp/TypeScript.webp'
import PortFolioImg from '../../source/webp/PortFoilo.webp'

const PortFolio = () => {

    return(
        <JaychisContainer>
            <img src = {PortFolioImg} width='100%' height='50%' style = {{marginTop: '1rem'}} alt = ' '/>
            <Title>
                
            </Title>
            <Content>
                 
            </Content>
            <Wrapper>
                <Wrapper>
                    <Column>
                        <Title>
                            
                        </Title>
                        <Content>
                            1
                        </Content>
                    </Column>

                    <Column>
                        <Title>
                            
                        </Title>
                        <Content>
                            25.04 ~ 25.04
                        </Content>
                    </Column>

                    <Column>
                        <Title>
                             
                        </Title>
                        <Content >
                            <a href = 'https://github.com/jungpill/JungPillPortFolio' target='_blank' rel="noreferrer">
                            https://github.com/jungpill/PortFolio
                            </a>
                        </Content>
                    </Column>
                </Wrapper>
            </Wrapper>
            <Title>
                 
            </Title>
            <Wrapper>
            <Content>
                <CustomTooltip title="React" position="bottom"><Icon src = {React} alt = ' '/></CustomTooltip>
            </Content>
            <Content>
                <CustomTooltip title="TypeScript" position="bottom"><Icon src = {TpyeScript} alt = ' '/></CustomTooltip>
            </Content>
            <Content>
                <CustomTooltip title="Zustand" position="bottom"><Icon src = {Zustand} alt = ' '/></CustomTooltip>
            </Content>
            <Content>
                <CustomTooltip title="styled-component" position="bottom"><Icon src = {styledComponent} alt = ' '/></CustomTooltip>
            </Content>
            <Content>
                <CustomTooltip title="Framer-Motion" position="bottom"><Icon src = {FramerMotion} alt = ' '/></CustomTooltip>
            </Content>
            
            </Wrapper>
            <Hr/>
            <Title style = {{marginBottom:'1rem'}}>
                
            </Title>
            <Content>
                1.   webp  <br/>
            </Content>
            <Li>
              webp       ,   .
            </Li>
            <br/>

            <Content>
                2.   UI/UX 
            </Content>
            <Li>
                         .
            </Li>
            <br/>

            <Content>
                3.   CI / CD 
            </Content>
            <Li>
            GitHub Actions  CI/CD        
            </Li>
            <br/>

        </JaychisContainer>
    )
}

export default PortFolio;

const JaychisContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
     &::-webkit-scrollbar {
    width: 0px;
  }
`

const Title = styled.div`
    font-weight: 600;
    font-size: 1rem;
    line-height: 1rem;
    margin-top: 1rem;
    color: #22222280;
    margin-right: 3rem;
    margin-bottom: 6px;
`

const Content = styled.div`
    font-weight: 700;
    font-size: .9rem;
    color: black
`

const Wrapper = styled.div`
    display: flex;
`

const Column = styled.div`
    display: flex;
    flex-direction: column;
    gap: 6px;
`

const Icon = styled.img`
    width: 60%;
    height: 60%;
    border-radius: 10px;
    --tw-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.1);
    --tw-shadow-colored: 0 4px 6px -1px var(--tw-shadow-color), 0 2px 4px -2px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), 
                var(--tw-ring-shadow, 0 0 #0000), 
                var(--tw-shadow);
    margin-top: .5rem;
`

const Hr = styled.hr`
    width: 100%;
    height: 0.1px;
    background-color: rgba(34,34,34)
`

const Li = styled.li`
    font-weight: 700;
    font-size: .8rem;
    line-height: 1.5rem;
    margin-left: .5rem;
    color: #22222280;
    margin-right: 3rem;
`
