import styled from "styled-components";
import { motion } from "framer-motion";

const CoverLetter = () => {


    return(
        <CoverLetterContainer
        initial={{ opacity: 0, x: 40 }}
        animate={{ opacity: 1, x: 0 }}
        exit={{ opacity: 0, x: -20 }}
        transition={{ duration: 0.5 }}
        >
            <CoverLetterWrapper>
                <Title>
                     
                </Title>
                <Text>
                     . 
                           
                      .    
                          
                            . 
                        
                                . 
                             
                           .
                </Text>
                <Title>
                      
                </Title>
                <Text>
                                 .
                          
                   .     
                         .  
                 1           
                           
                     .         
                         .         
                        .      
                              . 
                          
                         .
                </Text>
            </CoverLetterWrapper>
        </CoverLetterContainer>
    )
}

export default CoverLetter;


const CoverLetterContainer = styled(motion.div)`
    display: flex;
    width: 90%;
    height: 90%;
    background-color: white;
    border: 2px solid skyblue;
    border-radius: 20px;
    padding: 0.5rem;
    align-items: center;
    flex-direction: column;
    margin-left: 1%;
    padding-bottom: .8rem;
    overflow-y: scroll;
    
    &::-webkit-scrollbar {
    width: 0px;
  }
`

const CoverLetterWrapper = styled.div`
    display: flex;
    margin-left: 4%;
    flex-direction: column;
    width: 100%;
    height: 100%;
    
`

const Title = styled.div`
    font-weight: 600;
    line-height: 2rem;
    font-size: 1.2rem;
    margin: 5% 0 2% 0;
    color: #007AFF;
`

const Text = styled.div`
    font-weight: 600;
    line-height: 2rem;
    font-size: 1rem;
    width: 95%;
    word-wrap: break-word;
    word-break: keep-all; /*     */
    white-space: normal; /*     */
    text-align: justify;
`